import React from 'react'
import FriendFrame from './FriendFrame'

class List extends React.Component {
    
    constructor (props) {
        super(props)
    }

    render() {
        let list = ''
        if (this.props.friendList != undefined) {
            list = this.props.friendList.map((value, index) => 
            <FriendFrame key={index} name={value.name} email={value.email} changeCurrentRecipient={this.props.changeCurrentRecipient}></FriendFrame>
            )
        }
        return (
            <div className='d-flex row h-80-percent-oflow'>
                <div>
                    {list}
                </div>
            </div>
        )
    }

}


export default List